.country_page {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 0rem 0rem;
  overflow: hidden;
}
@media screen and (max-width: 1024px) and (min-width: 768px) {
  .country_page {
    margin: 0;
  }
}

@media screen and (max-width: 768px) and (min-width: 300px) {
  .country_page {
    margin: 0;
  }
}
