.weather {
  width: 100%;
  color: whitesmoke;
  margin: 1rem;
}

.weather_container {
  margin: auto;
  padding: 1rem;
}

.city {
  font-size: 2rem;
}

.weather_params_all {
  display: flex;
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: normal;
}

.weather_params_right {
  flex-grow: 1;
}
.weather_params_left {
  flex-grow: 1;
}

.weather_temp {
  font-size: 7rem;
  text-align: right;
}

.weather_icon {
  width: 6rem;
  margin-left: -1em;
  margin-top: -1em;
}
